@import url("https://cdn.jsdelivr.net/npm/bootstrap-icons@1.10.3/font/bootstrap-icons.css");

@font-face {
  font-family: 'LINESeedKR-Bd';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_11-01@1.0/LINESeedKR-Bd.woff2') format('woff2');
  font-weight: 100;
  font-style: normal;
}

* {
  margin: 0;
  padding: 0;
  font-family: 'LINESeedKR-Bd';
}

ul,
ol,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
  color: #333;
}

a:hover {
  color: var(--bs-danger);
}

.top_menu.fixed_top {
  position: -webkit-fixed;
  position: fixed;
  top: 0;
  background-color: #fff;
  border-bottom: 1px solid #eee;

  .navbar-nav {

    .nav-link {
      color: rgba(0, 0, 0, .55);
    }

    .nav-link.active {
      color: rgba(0, 0, 0, .55);
    }

    .nav-link:hover {
      color: var(--bs-danger);
    }
  }
}

.top_menu {
  width: 100%;
  position: absolute;
  top: 40px;
  left: 0;
  right: auto;
  z-index: 9;

  .navbar-brand {
    cursor: pointer;
  }

  .navbar-collapse {
    padding-top: 10px;
  }

  .icon {
    flex-direction: row;

    a {
      padding: 0 15px;
    }
  }

  .navbar-nav {

    .nav-link {
      color: rgba(255, 255, 255, 0.8);

      i {
        font-size: 2rem;
      }
    }

    .nav-link.active {
      color: rgba(255, 255, 255, 0.8);
    }
  }
}

.top_menu:hover {
  background-color: #fff;

  .navbar-nav {

    .nav-link {
      color: rgba(0, 0, 0, .55);
    }

    .nav-link:hover {
      color: var(--bs-danger);
    }
  }
}

.top_slide {
  width: 100%;
  height: 40px;
  line-height: 40px;
  background-color: var(--bs-danger);
  color: #fff;
  overflow: hidden;
  position: relative;
  text-align: center;
  pointer-events: none;
  font-size: 1vw;

  button {
    position: absolute;
    top: 0;
    left: auto;
    right: 0;
    height: 100%;
    pointer-events: auto;
  }
}

.weekly_best {
  margin: 50px 0;
  text-align: center;

  p {
    margin-top: 1rem;
  }

  h1 {
    padding: 20px 0;
    font-weight: bold;
  }

  .btn-group {
    margin-bottom: 20px;
    font-weight: bold;
  }

  .container {
    text-align: left;

    a {
      color: #000;
    }
  }

  img:hover {
    opacity: 0.8;
  }
}

.carousel {
  cursor: pointer;
}

.new_product {
  margin: 50px 0;
  text-align: center;

  p {
    margin-top: 1.2vw;
    font-size: 1.2vw;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  h4 {
    font-size: 2vw;
  }

  h1 {
    padding: 20px 0;
    font-weight: bold;
  }

  .container {
    text-align: left;
    cursor: pointer;
  }

  img:hover {
    opacity: 0.8;
  }
}

.photo_review {
  margin: 50px 0;
  text-align: center;

  .container {
    cursor: pointer;

    img:hover {
      opacity: 0.8;
    }
  }

  h1 {
    padding: 20px 0;
    font-weight: bold;
  }

  .pagination {
    margin: 20px 0;
    justify-content: center;

    .page-link {
      color: var(--bs-danger);
    }

    span.page-link {
      color: #fff;
      background-color: var(--bs-danger);
    }

    .page-item.disabled .page-link {
      color: #6c757d;
      pointer-events: none;
      background-color: #fff;
      border-color: #dee2e6;
    }
  }

  div.modal_wrap {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    background: rgba(0, 0, 0, 0.8);
    z-index: 10;
    text-align: left;

    .review_img {
      display: flex;
      justify-content: center;
      align-items: center;
      min-height: 300px;

      img {
        width: 60%;
      }
    }

    .review_info {
      background-color: #fff;
      height: 100vh;
      padding: 0;

      i.bi-star-fill {
        color: gold;
      }

      .review_title {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        padding: 30px 50px;
        border-bottom: 1px solid #6c757d;

        h6 {
          display: inline-flex;
          flex-direction: column;
          margin-bottom: 0;

          span {
            font-size: 14px;
            color: #6c757d;
            margin-top: 5px;
          }
        }
      }

      .review_content {
        padding: 30px 50px;

        .stars {
          display: flex;
          justify-content: space-between;
          align-items: center;

          u {
            text-decoration: none;
            font-size: 14px;
            color: #6c757d;
          }
        }

        .thumbs {
          color: #6c757d;
          cursor: pointer;

          span {
            margin-right: 15px;

            i {
              margin-right: 5px;
            }
          }

          span:hover,
          span.active {
            color: #000;
          }
        }
      }
    }
  }
}

.trend_pick {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  h1 {
    text-align: center;
    padding: 20px 0;
    font-weight: bold;
    width: 100%;
  }

  .card {
    cursor: pointer;
    margin-bottom: 50px;
    border: none;
    font-size: 2vw;

    div {
      width: 100%;
      height: auto;
      overflow: hidden;

      img {
        transition: all 0.3s;
      }

      img:hover {
        opacity: 0.8;
        transform: scale(1.1);
        transition: all 0.3s;
      }
    }
  }

  .trend {
    &1 {
      width: 50%;
      display: flex;
      flex-direction: column;
      align-items: start;
    }

    &2 {
      margin-top: 50px;
      width: 50%;
      display: flex;
      flex-direction: column;
      align-items: end;
    }
  }
}

.look_book {
  margin-bottom: 100px;

  h1 {
    text-align: center;
    padding: 20px 0;
    font-weight: bold;
  }

  .swiper {
    cursor: pointer;

    .swiper-wrapper {

      .swiper-slide {
        position: relative;
        overflow: hidden;

        img {
          transition: all 0.3s;
        }

        div {
          width: 100%;
          height: 100%;
          position: absolute;
          top: 0;
          left: 0;
          color: #fff;
          background-color: rgba(0, 0, 0, 0.2);
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          transition: all 0.3s;

          p {
            font-size: 1.2vw;
          }

          h3 {
            font-size: 2vw;
          }
        }
      }

      .swiper-slide:hover {

        img {
          transform: scale(1.2);
          transition: all 0.3s;
        }

        div {
          background-color: rgba(0, 0, 0, 0.5);
          transition: all 0.3s;
        }
      }
    }
  }
}

.footer {
  font-size: 0.8rem;
  padding-top: 10px;

  p {
    margin: 0;
    color: #aaa;
  }

  ul {
    text-align: right;

    li {

      a {
        color: #aaa;
      }

      a:hover {
        color: #000;
      }
    }
  }

  .black {
    color: #000;
  }

  .foot_info {
    p {
      i {
        display: inline-block;
        width: 1px;
        height: 11px;
        line-height: 11px;
        background-color: #aaa;
        margin: 0 8px;
      }
    }
  }

  .dropdown {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    height: 100%;

    #nav-dropdown {
      text-align: center;
      border: 1px solid #aaa;
      border-radius: 5px;
      width: 110px;
      height: 30px;
      padding: 0;
      line-height: 30px;
      display: inline-block;
      color: #000;
    }
  }

  nav {
    border-top: 1px solid #aaa;

    nav {
      border-top: none;

      a.nav-link {
        display: inline-flex;

        i {
          display: inline-block;
          width: 20px;
          height: 20px;
          margin: 0 5px;
        }

        .icon_ {
          &lg {
            background: url('../public/img/bt_icon_lgec.png') center no-repeat;
            background-size: contain;
          }

          &app1 {
            background: url('../public/img/bt_icon_app01.png') center no-repeat;
            background-size: contain;
          }

          &app2 {
            background: url('../public/img/bt_icon_app02.png') center no-repeat;
            background-size: contain;
          }

          &glo {
            background: url('../public/img/bt_icon_global.png') center no-repeat;
            background-size: contain;
          }
        }
      }


    }

    p {
      font-size: 0.8rem;
    }
  }

  .go_top {
    position: fixed;
    bottom: 100px;
    right: 50px;
    width: 70px;
    height: 70px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--bs-danger);
    color: #fff;
    z-index: 9;
    cursor: pointer;
    transition: all 0.5s;
  }

  .go_top:hover {
    background-color: #fff;
    color: var(--bs-danger);
    border: 1px solid var(--bs-danger);
  }
}

