div.banner {
  width: 100%;
  height: 74px;
  text-align: center;
  background-color: var(--bs-danger);
}


//detail

.start {
  opacity: 0;
  transform: scale(0);
}

.end {
  opacity: 1;
  transform: scale(1);
  transition: all 0.5s;
}

div.detail {
  margin-top: 70px;

  .info {
    position: relative;

    .center_img {
      opacity: 0.5;
    }

    div {
      position: absolute;
      top: 50%;
      left: 50%;
      right: auto;
      transform: translate(-50%,-50%);
      text-align: center;
    }
  }
}

ul.nav-tabs {

  li {

    button.nav-link {
      color: var(--bs-danger);
    }
  }
}

div.tab-content {
  text-align: center;

  .tab2 {

    table {

      th {
        background-color: rgb(248, 249, 250);
      }

      td {
        color: #666;
      }
    }

    p {
      font-size: 14px;
      color: #666;
      text-align: left;
    }

    ul {
      font-size: 14px;
      color: #666;
      text-align: left;
    }

    div.size_info {
      margin-top: 1rem;
      font-size: 14px;
      color: #666;
      text-align: left;

      span {
        color: #000;
      }
    }
  }

  .tab3 {
    text-align: left;
    font-size: 14px;

    h4 {
      text-align: center;
      padding: 20px 0;
    }

    span {
      color: var(--bs-danger);
    }
  }
}


//cart
.cart {
  text-align: center;

  table.cart_table {
    margin-top: 100px;

    tbody {
      
      button {
        width: 25px;
        height: 25px;
        border: none;
        background-color: var(--bs-danger);
        color: #fff;
      }
  
      span {
        display: inline-block;
        text-align: center;
        width: 30px;
      }
    }
  }
}

@media (max-width:610px) {
  .cart {
    table.cart_table {
      font-size: 2vw;

      tbody {

        tr {

          td {
            vertical-align: middle;

            span {
              width: 100%;
            }
          }
          
        }
      }
    }
  }
}


//login
form {
  width: 100%;
  height: 300px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

